import {usersAPI} from "../API/api";

const FOLLOW='FOLLOW'
const UNFOLLOW='UNFOLLOW'
const SET_USERS='SET_USERS'
const SET_CURRENT_PAGE='SET_CURRENT_PAGE'
const SET_TOTAL_USERS_COUNT='SET_TOTAL_USERS_COUNT'
const TOGGLE_IS_FETCHING='TOGGLE_IS_FETCHING'
const TOGGLE_IS_FOLLOWING='TOGGLE_IS_FOLLOWING'

let initialstate={
    users:[],
    pageSize: 5,
    totalUsersCount: 21,
    currentPage:1,
    isFetching:true,
    followingInProgress:[]
}
const usersReducer = (state=initialstate,action)=>{
    switch (action.type){
        case FOLLOW:
            return {...state,
                users:state.users.map(u=>{
                if (u.id===action.userID){
                    return {...u,followed:true}
                }
                return u})};
        case UNFOLLOW:
            return {...state,
                users:state.users.map(u=>{
                    if (u.id===action.userID){
                        return {...u,followed:false}
                    }
                    return u})};
        case SET_USERS:
            return{...state,users:action.users}
        case SET_CURRENT_PAGE: return{...state,currentPage: action.currentPage}
        case SET_TOTAL_USERS_COUNT:return{...state,totalUsersCount: action.totalUsersCount}
        case TOGGLE_IS_FETCHING:return {...state,isFetching: action.isFetching}
        case TOGGLE_IS_FOLLOWING:
            return {
                ...state,
                followingInProgress:action.isFetching
                    ? [...state.followingInProgress,action.userId]
                    :state.followingInProgress.filter(id=> id!=action.userId)}

        default:
            return state
    }
}
export const followSuccess=(userID)=>{return{type:FOLLOW,userID}}
export const unfollowSuccess=(userID)=>{return{type:UNFOLLOW, userID}}
export const setUsers=(users)=>{return{type:SET_USERS, users}}
export const setCurrentPage=(currentPage)=>{return{type:SET_CURRENT_PAGE,currentPage}}
export const setTotalUsersCount=(totalUsersCount)=>{return{type:SET_TOTAL_USERS_COUNT,totalUsersCount}}
export const toggleIsFetching=(isFetching)=>{return{type:TOGGLE_IS_FETCHING,isFetching}}
export const toggleIsFollowing=(isFetching,userId)=>{return{type:TOGGLE_IS_FOLLOWING,isFetching,userId }}

 export const getUsers=(currentPage,pageSize) =>{
   return async (dispatch)=>{
        dispatch(toggleIsFetching(true));
        let data=await usersAPI.getUsers(currentPage, pageSize);
       dispatch(toggleIsFetching(false));
       dispatch(setUsers(data.items));
       dispatch(setTotalUsersCount(data.totalCount));

    }
}
export const follow = (userId) =>{
    return async (dispatch)=>{
        dispatch(toggleIsFollowing(true,userId))
        let response = await usersAPI.follow(userId)
        if (response.data.resultCode==0){
            dispatch(followSuccess(userId))}
        dispatch(toggleIsFollowing(false,userId))
    }
}

export const unfollow = (userId) =>{
    return async (dispatch)=>{
        dispatch(toggleIsFollowing(true,userId))
        let response = await usersAPI.unfollow(userId)
        if (response.data.resultCode==0){
            dispatch(unfollowSuccess(userId))}
        dispatch(toggleIsFollowing(false,userId))
    }
}

export default usersReducer