import './App.css';
import React, {Suspense} from "react";
import Nav from "./components/Nav/Nav";
import News from "./components/News/News";
import Music from "./components/Music/Music";
import Settings from "./components/Settings/Settings";
import {Routes, Route} from "react-router-dom"
// import DialogsConteiner from "./components/Dialogs/DialogsConteiner";
// import UsersConteiner from "./components/Users/UsersConteiner";
import  {withRouter} from "./components/Profile/ProfileConteiner";
import HeaderConteiner from "./components/Header/HeaderConteiner";
import Login from "./components/Login/login";
import {connect} from "react-redux";
import {compose} from "redux";
import {initializeApp} from "./redux/appReducer";
import Preloader from "./components/common/preloader";
const DialogsConteiner = React.lazy(() => import('./components/Dialogs/DialogsConteiner'));
const UsersConteiner = React.lazy(() => import('./components/Users/UsersConteiner'));
const ProfileConteiner= React.lazy(() => import('./components/Profile/ProfileConteiner'));
class App extends React.Component {
    componentDidMount() {
        this.props.initializeApp()
    }
    render() {
        if(!this.props.initialized){
            return <Preloader/>
        }
        return (
            <div className='app-wrapper'>
                <HeaderConteiner/>
                <Nav/>
                <div className='app-wrapper-content'>
                    <Suspense fallback={<Preloader/>}>
                        <Routes>
                            <Route path='/dialogs/*' element={<DialogsConteiner/>}/>
                            <Route path='/profile/:userId?'
                                   element={<ProfileConteiner/>}
                            />
                            <Route path='/news' element={<News/>}/>
                            <Route path='/music' element={<Music/>}/>
                            <Route path='/settings' element={<Settings/>}/>
                            <Route path='/users' element={<UsersConteiner/>}/>
                            <Route path='/login' element={<Login/>}/>
                        </Routes>
                    </Suspense>
                </div>
            </div>
        );
    }
}
const mapStateToProps=(state)=>({
    initialized:state.app.initialized
})
export default compose(withRouter,connect (mapStateToProps,{initializeApp}))(App);
